

.App{ 
  .Main{
    padding: 0;
    margin: 0;
    .header{
      background-color: black;
      color: #fff;
      padding: 1em;
      text-align: center;
      h1{
        font-size: 1.5em;
        width: 100%;
      }
    }
    .fa-bars{
      position: fixed;
      top: 10px;
      right: 5px;
      width: 40px;
      font-size: 25px;
      background-color: #777;
      color: #fff;
      text-align: center;
    }
    nav{
      padding: 1em 0;
      ul{
        position: fixed;
        top: 30px;
        background-color: #777;
        width: 100%;
        text-align: center;
        display: none;
        padding: 4em 0em;
        li{
          a{
            color: #fff;
            &:hover{
              color: #777;
            }
          }
        }
      }
      .display{
        display: block;
      }
    }
    .search{
      padding: 0;
      .input-text{
        display: flex;
        justify-content: space-between;
        input[type=text]{
          width: 70%;
        }
        input[type=number]{
          width: 30%;
        }
      }
    }
    .loading{
      width: 50%;
    }
    .jumbotron{
      margin: 2em 0;
      padding: 0;
      p{
        margin: 0 auto;
        font-size: 14px;
        margin-left: 15px;
      }
      
      .title{
        font-size: 12px;
        margin: 5px;
      }
      .plot{
        margin: 5px;
       font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      }
     
     
    }
    .imgPoster{
      border: 2px solid #000;
      width: 100%;
    }
    

    .moviecard{
      margin: 1em 0;
    }
    .card{
      padding: 3em 2em;
      margin: 1em 0 ;
      text-align: center;
    }
  }
}

@media(min-width:500px){
  
html{
  padding: 0;
  margin: 0;
}
.App{ 
  max-width: 70%;
  width: 70%;
  margin: 0 auto;
  position: relative;
  .Main{
    width: 80%;
    margin: 0 auto;
    .header{
      width: 100%;
      margin: 0 auto;
      padding-top: 4em;
    }
    .container{
      width: 100%;
      margin: 0 auto;
      padding: 0;
      margin: 0;
      position: relative;
      .fa-bars{
        display: none;
      }
      nav{
        padding: 1em 0;
        
        ul{
          width: 55%;
          position: fixed;
          top: 0;
          background-color: #777;
          text-align: center;
          display: block;
          padding: 0;
          display: flex;
          justify-content: space-between;
          li{
            a{
            }
          }
        }
        .display{
          display: block;
        }
      }
      .search{
        max-width: 700px;
        
        .input-text{
          display: flex;
          justify-content: space-between;
          input[type=text]{
            width: 70%;
          }
          input[type=number]{
            width: 30%;
          }
        }
      }
      .loading{
        width: 50%;
        max-width: 700px;
      }
      .jumbotron{
        max-width: 700px;
        margin: 2em 0;
        padding: 0;
        p{
          margin: 0 auto;
          font-size: 14px;
          margin-left: 15px;
        }
        
        .title{
          font-size: 12px;
          margin: 5px;
        }
        .plot{
          margin: 5px;
         font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        }
       
      }
      .imgPoster{
        border: 2px solid #000;
        width: 100%;
        height: 50%;
      }
      
  
      .moviecard{
        margin: 1em 0;
      }
      .card{
        padding: 3em 2em;
        margin: 1em 0 ;
        text-align: center;
        max-width: 700px;
      }
    }
    }
   
}

}